
#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}
p{
    text-shadow: 0 0 5px rgba(0,0,0,.5);
}
a{
    color: #42b983;
}
.fade-enter{
    opacity: 0;
}
.fade-leave{
    opacity:1;
}
.fade-enter-active{
    -webkit-transition: opacity .5s;
    transition: opacity .5s;
}
.fade-leave-active{
    opacity: 0;
    -webkit-transition: opacity .5s;
    transition: opacity .5s;
}

